import React, {useContext} from 'react';
import styled from 'styled-components';
import { Select, InputNumber, Divider, Row, Col } from 'antd';
import Sink from './Sink';
import Skirting from './Skirting';
import SmartBar from './SmartBar';
import CheckEstimate from './CheckEstimate';
import Profile from './Profile';

const EstimateListBlock = styled.div`
  flex: 1;
  padding: 20px 32px;
  padding-bottom: 48px;
  overflow-y: auto;
`;


function EstimateList() {
    return (
    <EstimateListBlock>
      <Sink />
      <Divider />
      <Skirting />
      <Divider />
      <SmartBar />
      <Divider />
      <CheckEstimate />
      <Divider />
      <Profile />
    </EstimateListBlock>);
}

export default EstimateList;