import React, { useState, useContext, useEffect } from 'react';
import { Button, Table, Row, Col, Switch, Space } from 'antd';
import { Context } from './ContextProvider';
import styled from 'styled-components';
import _ from 'lodash';

const columns = [
  {
    title: '타입',
    dataIndex: 'type',
    width: '40%',
  },
  {
    title: '가로',
    dataIndex: 'width',
  },
  {
    title: '세로',
    dataIndex: 'height',
  },
  {
    title: '길이',
    dataIndex: 'length',
  },
  {
    title: '가격',
    dataIndex: 'price',
  },
];
export const MyTable = styled(Table)`
  tbody {
    tr {
      td {
        font-size: 8px;
        height: 1px;
        padding: 4px;
      }
    }
  }
`
function CheckEstimate() {
    const { state, contextDispatch } = useContext(Context);
    const [selectedRowKeys, setSelectedRowKeys] = useState([]);
    const [loading1, setLoading1] = useState(false);
    const [loading2, setLoading2] = useState(false);
    const [data, setData] = useState(state.estimateList);
    const [totalPrice, setTotalPrice] = useState(0);

    useEffect(() => {
      setData(state.estimateList);
      let sum = 0;
      for (const obj of state.estimateList) {
        sum += obj.price;
      }
      setTotalPrice(sum);
    }, [state]);

    const deleteChecked = (v) => {
      setLoading1(true);
        setTimeout(() => {
          setSelectedRowKeys([]);
          setLoading1(false);
        }, 200);
        let list = _.cloneDeep(state.estimateList);
        for (var item in list) {
          for (var k in selectedRowKeys) {
            if(list[item].key == selectedRowKeys[k]) {
              list.splice(item, 1);
            }
          }
        }
        contextDispatch({ type: "DELETE", value: list });
    };

    const initiate = () => {
      setLoading2(true);
      setTimeout(() => {
        setSelectedRowKeys([]);
        setLoading2(false);
      }, 200);
      contextDispatch({ type: "DELETEALL" });
  };

    const onSelectChange = (newSelectedRowKeys) => {
      setSelectedRowKeys(newSelectedRowKeys);
    };

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
    };

    const hasSelected = selectedRowKeys.length > 0;

    return <>
        <div style={{ marginBottom: 16, }} >
            <span
            style={{
                marginLeft: 8,
            }}
            >
            {hasSelected ? `Selected ${selectedRowKeys.length} items` : ''}
            </span>
        </div>
        <Space align="center" style={{ marginBottom: 16 }}>
          총 금액 : {totalPrice}원
        </Space>
        <MyTable rowSelection={rowSelection} columns={columns} dataSource={data} />

        <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
            <Col xs={{ span: 11, offset: 1 }} lg={{ span: 7, offset: 3 }}>
            <Button 
                    type="primary" 
                    onClick={deleteChecked} 
                    disabled={!hasSelected} 
                    loading={loading1} 
                    style={{width: "80%", marginLeft: "10px", marginTop: "10px"}}
                >
                    선택 삭제
                </Button>
            </Col>
            <Col xs={{ span: 11, offset: 1 }} lg={{ span: 7, offset: 3 }}>
                <Button 
                    type="primary" 
                    onClick={initiate} 
                    loading={loading2} 
                    style={{width: "80%", marginTop: "10px"}}
                >
                    견적 초기화
                </Button>
            </Col>
        </Row>
    </>

}

export default CheckEstimate;
