function Sink() {
    const { state, contextDispatch } = useContext(Context);
    const [typeASelection, setTypeASelection] = useState('-');
    const [typeBSelection, setTypeBSelection] = useState('-');
    const [premiumSelection, setPremiumSelection] = useState('-');
    const [width, setWidth] = useState(195);
    const [height, setHeight] = useState(800);

    const handleChange = (value, type) => {
        if (type == 'a'){
            setTypeASelection(value);
        } else if (type == 'b') {
            setTypeBSelection(value);
        } else if (type == 'p') {
            setPremiumSelection(value);
        }
    };
    const onChange = (value, type) => {
        if (type == 'w'){
            setWidth(value);
        } else if (type == 'h') {
            setHeight(value);
        }
    };

    const btnClick = () => {
        const itemList = [];
        let cnt = state.estimateList.length;

        if(typeASelection != '-') {
            itemList.push({
                'key': cnt++,
                'type': typeASelection,
                'width': width,
                'height': height,
                'length': '-',
                'price': width > 450 ? 25000 : 21000
            })
        }
        if(typeBSelection != '-') {
            itemList.push({
                'key': cnt++,
                'type': typeBSelection,
                'width': width,
                'height': height,
                'length': '-',
                'price': width > 450 ? 31000 : 27000
            })
        }
        if(premiumSelection != '-') {
            itemList.push({
                'key': cnt++,
                'type': premiumSelection,
                'width': width,
                'height': height,
                'length': '-',
                'price': width > 450 ? 37000 : 33000
            })
        }
        contextDispatch({ type: "ADD", value: itemList });
    }

    return <>
    <Title level={4} style={{paddingLeft: "2px"}}>싱크대 도어</Title>
    <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }} style={{padding: "2px"}}>
      <Col className="gutter-row" xs={20} lg={8}>
        타입 A <Select 
                defaultValue={typeASelection}
                style={{
                width: "100%",
                paddingDown: "8px",
                }}
                onChange={(v)=>{handleChange(v, 'a')}}
                options={optionAType}
            />
      </Col>
      <Col className="gutter-row" xs={20} lg={8}>
        타입 B <Select 
                defaultValue={typeBSelection}
                style={{
                width: "100%",
                paddingDown: "10px",
                }}
                onChange={(v)=>{handleChange(v, 'b')}}
                options={optionBType}
            />
      </Col>
      <Col className="gutter-row" xs={20} lg={8}>
        프리미엄 <Select 
                defaultValue={premiumSelection}
                style={{
                width: "100%",
                paddingDown: "10px",
                }}
                onChange={(v)=>{handleChange(v, 'p')}}
                options={premiumType}
            />
      </Col>
    </Row>

    <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }} style={{padding: "2px", marginTop: "5px"}}>
        <Col className="gutter-row" span={8}>
            가로 (195~450) 
            <InputNumber min={195} max={600} defaultValue={width} onChange={(v)=>{onChange(v, 'w')}} style={{ width: "80%", }}/>
        </Col>
        <Col className="gutter-row" span={8}>
            세로 (300~800)
            <InputNumber min={195} max={800} defaultValue={height} onChange={(v)=>{onChange(v, 'h')}} style={{ width: "80%", }}/>
        </Col>
    </Row>

    <Button type="primary" onClick={btnClick} size={10} style={{display: "flex", flexDirection:"column", margin: "10px auto"}}>
        견적서에 담기
    </Button>
    </>

}

export default Sink;