import React, { useState, useContext } from 'react';
import styled from 'styled-components';
import { Select, InputNumber, Divider, Row, Col, Button } from 'antd';
import { Typography } from 'antd';
import { Context } from './ContextProvider';

const { Title } = Typography;

const optionAType = [
    {
        label: "선택 안함",
        options: [
            {
                label: "-",
                value: "-"
            }
        ]
    },
    {
        label: "스마트바",
        options: [
            {
                label: "01.실버/무광",
                value: "01.실버/무광"
            },
            {
                label: "02.골드/무광",
                value: "02.골드/무광"
            },
            {
                label: "03.블랙/유광",
                value: "03.블랙/유광"
            },
            {
                label: "04.화이트/유광",
                value: "04.화이트/유광"
            }
        ]
    }
]

function SmartBar() {
    const { state, contextDispatch } = useContext(Context);
    const [type, setType] = useState('-');
    const [length, setLength] = useState(150);

    const handleChange = (v) => {
        setType(v);
    };
    const onChange = (v) => {
        setLength(v);
    };

    const btnClick = () => {
        const itemList = [];
        let cnt = state.estimateList.length;
        let price = (length / 50) * 1000;
        if(type != '-') {
            itemList.push({
                'key': cnt++,
                'type': type,
                'width': '-',
                'height': '-',
                'length': length,
                'price': price
            })
        }
        contextDispatch({ type: "ADD", value: itemList });
    }

    return <>
    <Title level={4} style={{paddingLeft: "2px"}}>스마트바</Title>
    <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }} style={{padding: "2px"}}>
      <Col className="gutter-row" span={12}>
        타입 <Select 
                defaultValue={type}
                style={{
                width: "100%",
                paddingDown: "10px",
                }}
                onChange={handleChange}
                options={optionAType}
            />
      </Col>
    </Row>

    <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }} style={{padding: "2px"}}>
    <Col className="gutter-row" span={12}>
        길이 (150mm~) 
        <InputNumber min={150} defaultValue={length} onChange={onChange} style={{ width: "100%", }}/>
      </Col>
    </Row>

    <Button type="primary" size={10} onClick={btnClick} style={{display: "flex", flexDirection:"column", margin: "10px auto"}}>
        견적서에 담기
    </Button>
    </>

}

export default SmartBar;