import React, { useReducer } from "react";
import _ from 'lodash';

export const Context = React.createContext();

const initialState = {
    estimateList: [],
};


const reducer = (state, action) => {
  switch (action.type) {
    case "ADD" :
      return {
        estimateList: [...state.estimateList, ...action.value], 
      };
    case "DELETE" :
        return {
          estimateList: _.cloneDeep(action.value), 
    };
    case "DELETEALL" :
        return {
          estimateList: [], 
    };
    default:
      throw new Error();
  }
};

const ContextProvider = ({ children }) => {
    const [state, contextDispatch] = useReducer(reducer, initialState);

    return (
        <Context.Provider value={{state, contextDispatch}}>
            {children}
        </Context.Provider>
    );
};

export default ContextProvider;